import request from '@/utils/request'
const groupName = 'collection'
export default {
	/**
	 * 
	 */
	changeCollection(collection) {
		return request({
			url: `/${groupName}/changeCollection`,
			method: 'post',
			data: collection
		})
	},
	/**
	 * 
	 * @param {} page
	 */
	getByPage(page) { // 
		return request({
			url: `/${groupName}/getByPage`,
			method: 'post',
			data: page
		})
	},
	/**
	 * ID
	 * @param {*} id
	 */
	getByProductId(id) {
		return request({
			url: `/${groupName}/getByProductId/${id}`,
			method: 'get'
		})
	},
	/**
	 * 
	 */
	getSumCollection() {
	  return request({
	    url: `/${groupName}/getSumCollection`,
	    method: 'get'
	  })
	}
}
