import request from '@/utils/request'
const groupName = 'user'
export default {
  /**
   * 
   */
  getMyWallet() {
    return request({
      url: `/${groupName}/getMyWallet`,
      method: 'get'
    })
  },
  /**
   * 
   */
  update(userDto) {
    return request({
      url: `/${groupName}/update`,
      method: 'post',
	  data: userDto
    })
  }
}
