import request from '@/utils/request'
const groupName = 'brand'
export default {
  /**
   * 
   */
  save(sysUser) {
    return request({
      url: `/${groupName}/save`,
      method: 'post',
      data: sysUser
    })
  },
  /**
  * 
  */
  update(sysUser) {
    return request({
      url: `/${groupName}/update`,
      method: 'put',
      data: sysUser
    })
  },
  /**
   * 
   */
  getByPage(page) {
    return request({
      url: `/${groupName}/getByPage`,
      method: 'post',
      data: page
    })
  },
  /**
   * 
   */
  deleteById(id) {
    return request({
      url: `/${groupName}/delete/${id}`,
      method: 'delete'
    })
  },
  /**
   * id
   */
  get(id) {
    return request({
      url: `/${groupName}/get/${id}`,
      method: 'get'
    })
  },
  /**
   * id
   */
  getByCategoryId(id) {
    return request({
      url: `/${groupName}/getByCategoryId/${id}`,
      method: 'get'
    })
  },
  /**
   * id
   */
  getUpdate(id) {
    return request({
      url: `/${groupName}/getUpdate/${id}`,
      method: 'get'
    })
  },
  /**
   * 
   */
  getByName(name) {
    return request({
      url: `/${groupName}/getByName/${name}`,
      method: 'get'
    })
  }
}
