import request from '@/utils/request'
const groupName = 'shopProductCategory'
export default {
	/**
	 * 
	 */
	save(sysUser) {
		return request({
			url: `/${groupName}/save`,
			method: 'post',
			data: sysUser
		})
	},
	/**
	 * 
	 */
	update(sysUser) {
		return request({
			url: `/${groupName}/update`,
			method: 'put',
			data: sysUser
		})
	},
	/**
	 * 
	 */
	getByPage(page) {
		return request({
			url: `/${groupName}/getByPage`,
			method: 'post',
			data: page
		})
	},
	/**
	 * 
	 */
	deleteById(id) {
		return request({
			url: `/${groupName}/delete/${id}`,
			method: 'delete'
		})
	},
	/**
	 * id
	 */
	get(id) {
		return request({
			url: `/${groupName}/get/${id}`,
			method: 'get'
		})
	},
	/**
	 * id
	 */
	getCategoryAndBrandById(id) {
		return request({
			url: `/${groupName}/getCategoryAndBrandById/${id}`,
			method: 'get'
		})
	},
	/**
	 * 
	 */
	getTree() {
		return request({
			url: `/${groupName}/getTree`,
			method: 'get'
		})
	},
	/**
	 * 
	 */
	getSelectTree() {
		return request({
			url: `/${groupName}/getSelectTree`,
			method: 'get'
		})
	},
	/**
	 * 
	 */
	getAllSecond() {
		return request({
			url: `/${groupName}/getAllSecond`,
			method: 'get'
		})
	},
	/**
	 * 
	 */
	getNavList() {
		return request({
			url: `/${groupName}/getNavList`,
			method: 'get'
		})
	}
}
