import request from '@/utils/request'
const groupName = 'history'
export default {
  /**
   * 
   */
  save(history) {
    return request({
      url: `/${groupName}/save`,
      method: 'post',
      data: history
    })
  },
  /**
   * 
   * @param {} page
   */
  getByPage(page) { // 
  	return request({
  		url: `/${groupName}/getByPage`,
  		method: 'post',
  		data: page
  	})
  },
  /**
   * id
   */
  deleteHistoryById(id) {
	return request({
	  url: `/${groupName}/deleteHistoryById/${id}`,
	  method: 'delete'
	})  
  },
  /**
   * 
   */
  clearHistory() {
    return request({
      url: `/${groupName}/clearHistory`,
      method: 'delete'
    })
  },
  /**
   * 
   */
  getSumHistory() {
    return request({
      url: `/${groupName}/getSumHistory`,
      method: 'get'
    })
  }
}
