import request from '@/utils/request'
const groupName = 'product'
export default {
	/**
	 * 
	 */
	save(sysUser) {
		return request({
			url: `/${groupName}/save`,
			method: 'post',
			data: sysUser
		})
	},
	/**
	 * 
	 * @param {} page
	 */
	getByPage(page) { // 
		return request({
			url: `/${groupName}/getByPage`,
			method: 'post',
			data: page
		})
	},
	/**
	 * ID
	 * @param {} ids
	 */
	getByIds(ids) {
		return request({
			url: `/${groupName}/getByIds`,
			method: 'post',
			data: ids
		})
	},
	/**
	 * 
	 * @param {} page
	 */
	getByPageHasNotPack(page) { // 
		return request({
			url: `/${groupName}/getByPageHasNotPack`,
			method: 'post',
			data: page
		})
	},
	/**
	 * 
	 */
	update(sysUser) {
		return request({
			url: `/${groupName}/update`,
			method: 'put',
			data: sysUser
		})
	},
	/**
	 * 
	 * @param {*} id
	 */
	getUpdate(id) {
		return request({
			url: `/${groupName}/getUpdate/${id}`,
			method: 'get'
		})
	},
	/**
	 * ID
	 * @param {*} id
	 */
	get(id) {
		return request({
			url: `/${groupName}/get/${id}`,
			method: 'get'
		})
	},
	/**
	 * 
	 * @param {*} id
	 */
	getProductDetailList(id) {
		return request({
			url: `/${groupName}/getProductDetailList/${id}`,
			method: 'get'
		})
	},
	/**
	 * 
	 * @param {*} id
	 */
	delete(id) {
		return request({
			url: `/${groupName}/delete/${id}`,
			method: 'delete'
		})
	},
	/**
	 * 
	 */
	publish(id) {
		return request({
			url: `/${groupName}/publish/${id}`,
			method: 'put'
		})
	},
	/**
	 * 
	 */
	unPublish(id) {
		return request({
			url: `/${groupName}/unPublish/${id}`,
			method: 'put'
		})
	},
	/**
	 * 
	 */
	getNewProduct() {
		return request({
			url: `/${groupName}/getNewProduct`,
			method: 'get'
		})
	},
	/**
	 * 
	 */
	getRecommendList() {
		return request({
			url: `/${groupName}/getRecommendList`,
			method: 'get'
		})
	},
	/**
	 * 
	 */
	getRankByProduct(productId) {
		return request({
			url: `/${groupName}/getRankByProduct/${productId}`,
			method: 'get'
		})
	},
	/**
	 * 
	 */
	search(data) {
		return request({
			url: `/${groupName}/search`,
			method: 'post',
			data: data
		})
	},
}
