import request from '@/utils/request'
const groupName = 'shopCoupon'
export default {
  /**
   * 
   */
  save(sysUser) {
    return request({
      url: `/${groupName}/save`,
      method: 'post',
      data: sysUser
    })
  },
  /**
   * 
   * @param {Object} sysUser
   */
  catchCoupon(sysUser) {
    return request({
      url: `/${groupName}/catchCoupon`,
      method: 'post',
      data: sysUser
    })
  },
  /**
   * 
   * @param {} page
   */
  getByPage(page) { // 
    return request({
      url: `/${groupName}/getByPage`,
      method: 'post',
      data: page
    })
  },
  /**
   * 
   * @param {*} id
   */
  delete(id) {
    return request({
      url: `/${groupName}/delete/${id}`,
      method: 'delete'
    })
  },
  /**
   * 
   */
  down(id) {
    return request({
      url: `/${groupName}/down/${id}`,
      method: 'put'
    })
  },
  /**
   * 
   */
  getProductCoupon(id) {
    return request({
      url: `/${groupName}/getProductCoupon/${id}`,
      method: 'get'
    })
  },
  /**
   * 
   */
  getAllProductCoupon() {
    return request({
      url: `/${groupName}/getAllProductCoupon`,
      method: 'get'
    })
  }
}
