import request from '@/utils/request'
const groupName = 'wx'
export default {
	/**
	 * code
	 */
	loginByCode(code) {
		return request({
			url: `/${groupName}/wxLogin/${code}`,
			method: 'get'
		})
	},
	/**
	 * 
	 * @param {Object} user
	 */
	registerLogin(user) {
		return request({
			url: `/${groupName}/registerLogin`,
			method: 'post',
			data: user
		})
	},
	/**
	 * 
	 */
	bindUser(user) {
		return request({
			url: `/${groupName}/bindUser`,
			method: 'post',
			data: user
		})
	},
	// 
	getLoginInfo() {
		return request({
			url: `/${groupName}/info`,
			method: 'get'
		})
	},
	/**
	 * 
	 * @param {Object} shopUserLoginDto
	 */
	wxLoginByPhone(shopUserLoginDto) {
		return request({
			url: `/${groupName}/wxLoginByPhone`,
			method: 'post',
			data: shopUserLoginDto
		})
	},
}
